import ssPaymentPassword from '../../components/sanshui-payment-password'
import jwx from '../../common/weiPay/jws.js'
export default {
	components: {
		ssPaymentPassword
	},
	data() {
		return {
			order_id: 0,
			user_id: 0,
			payItem: [{
				id: 1,
				name: "支付宝"
			}, {
				id: 2,
				name: "微信"
			}, {
				id: 3,
				name: "银行卡"
			}],
			payMain: {
				id: 0,
				name: "",
				zf_imgse: "",
				zf_name: ''
			}, //支付方式
			mainData: {
				orderData: {
					order_state: 0
				},
				buyersUser: {
					user_name: ''
				},
				sellerUser: {
					user_name: ''
				},
				sellerPayData: []
			},
			transferData: {
				order_price: 0,
				transfer_price: 0,
				pay_price: 0,
				pay_type: 2 //1、金元宝；2、佣金
			},
			status: 1,
			payitem_start: '',
			payitem_end: '',
			liveCountdown: '', //倒计时
			payTypeIndex: 0 //支付方式
		};
	},
	onShow() {
		this.$store.commit('judgeLogin'); //判断登录状态
	},
	onLoad(option) {
		if (option.orderId != '') this.order_id = option.orderId;
		var user = uni.getStorageSync('uerInfo');
		this.user_id = user.id;

		this.initData();
	},
	methods: {
		payTypeChange(e) {
			this.payTypeIndex = e.target.value;
			this.payMain = this.mainData.sellerPayData[this.payTypeIndex];
		},
		call(phone) { //拨打电话
			if (phone === "" || phone === "0")
				return false;
			uni.makePhoneCall({
				phoneNumber: phone,
				success: (res) => {
					console.log('调用成功!')
				},
				fail: (res) => {
					console.log('调用失败!')
				}

			});
		},
		initData() {
			//查询数据
			var _this = this;
			uni.showLoading({
				title: '数据查询中'
			});
			_this.$request.order
				.particulars({
					order_id: _this.order_id
				})
				.then(data => {
					uni.hideLoading();
					if (data.status == 1) {
						_this.mainData = data.data;
						if (_this.mainData.sellerUser == null) {
							_this.mainData.sellerUser = {
								sellerUserId: 0
							};
						}
						//收款码
						if (_this.mainData.sellerPayData.length > 0) {
							_this.mainData.sellerPayData.forEach(item => {
								item.name = _this.payItem.find(p => p.id == item.type).name;
							})
							_this.payMain = _this.mainData.sellerPayData[0];
						}
						//订单时间
						if (_this.mainData.orderData.order_state == 1) {
							var date_start = getDate(_this.getDate(_this.mainData.orderData.time * 1000));
							_this.payitem_start = this.add0(date_start.getHours()) + ":" + this.add0(date_start
									.getMinutes()) +
								":" + this.add0(date_start.getSeconds());

							var str_date_end = _this.getDate(_this.mainData.orderData.time * 1000 + 3600 * 1000);
							var date_end = getDate(str_date_end);
							_this.payitem_end = this.add0(date_end.getHours()) + ":" + this.add0(date_end
									.getMinutes()) +
								":" + this.add0(date_end.getSeconds());

							//倒计时
							this.getLiveTimeCount(str_date_end);
						}
					} else {
						uni.showToast({
							icon: 'none',
							title: data.msg
						});
					}
				});
		},
		confirmOrderPay() {
			//确认订单
			var _this = this;
			uni.showModal({
				title: '确认收款',
				content: '是否确定已收到款？未收到款确认将造成自己财产损失，请谨慎点击。',
				success: function(res) {
					if (res.confirm) {
						uni.showLoading({
							title: '数据提交中'
						});
						_this.$request.order
							.setPayDocument({
								order_id: _this.order_id,
								order_state: 3
							})
							.then(data => {
								uni.hideLoading();
								if (data.status == 1) {
									uni.showToast({
										icon: 'none',
										title: '确认成功'
									});
									setTimeout(() => {
										_this.initData();
									}, 1000)
								} else {
									uni.showToast({
										icon: 'none',
										title: data.msg
									});
								}
							})
							.catch(err => {
								uni.hideLoading();
								//消息异常
								uni.showToast({
									icon: 'none',
									title: '数据加载异常'
								});
							});
					}
				}
			})
		},
		confirmOrder() {
			//确认订单
			var _this = this;
			uni.showModal({
				title: '确认订单',
				content: '是否确定已收到商品？未收到商品确认将造成自己财产损失，请谨慎点击。',
				success: function(res) {
					if (res.confirm) {
						uni.showLoading({
							title: '数据提交中'
						});
						_this.$request.order
							.receivingGoods({
								order_id: _this.order_id
							})
							.then(data => {
								uni.hideLoading();
								if (data.status == 1) {
									uni.showToast({
										icon: 'none',
										title: '确认成功'
									});
									setTimeout(() => {
										_this.initData();
									}, 1000)
								} else {
									uni.showToast({
										icon: 'none',
										title: data.msg
									});
								}
							})
							.catch(err => {
								uni.hideLoading();
								//消息异常
								uni.showToast({
									icon: 'none',
									title: '数据加载异常'
								});
							});
					}
				}
			})
		},
		cancel() {
			//取消订单
			var _this = this;
			uni.showModal({
				title: '取消订单',
				content: '是否确定关闭此订单？',
				success: function(res) {
					if (res.confirm) {
						uni.showLoading({
							title: '数据提交中'
						});
						_this.$request.order
							.delOrder({
								order_id: _this.order_id
							})
							.then(data => {
								uni.hideLoading();
								if (data.status == 1) {
									uni.showToast({
										icon: 'none',
										title: '取消成功'
									});
									setTimeout(() => {
										_this.initData();
									}, 1000)
								} else {
									uni.showToast({
										icon: 'none',
										title: data.msg
									});
								}
							})
							.catch(err => {
								uni.hideLoading();
								//消息异常
								uni.showToast({
									icon: 'none',
									title: '数据加载异常'
								});
							});
					}
				}
			})
		},
		pay() {
			//支付
			var _this = this;
			uni.showModal({
				title: '已付款，提醒卖家收款',
				content: '请再次确认您是否付款，未成功就点击，将记录违规，请谨慎点击',
				success: function(res) {
					if (res.confirm) {
						uni.navigateTo({
							url: '/pages/order/pay?orderId=' + _this.order_id
						});
					}
				}
			})
		},
		transfer() {
			//转拍
			var _this = this;
			_this.$request.order
				.resell({
					order_id: _this.order_id
				})
				.then(data => {
					uni.hideLoading();
					if (data.status == 1) {
						_this.transferData.order_price = data.data.unitPurchasePrice;
						_this.transferData.transfer_price = data.data.unitPrice;
						_this.transferData.pay_price = data.data.serviceCharge;
						this.$refs['showtransfer'].open();
					} else {
						uni.showToast({
							icon: 'none',
							title: data.msg
						});
					}
				})
				.catch(err => {
					uni.hideLoading();
					//消息异常
					uni.showToast({
						icon: 'none',
						title: '数据加载异常'
					});
				});
		},
		substitution() {
			//置换成银元宝
			var _this = this;
			uni.showModal({
				title: '取消订单',
				content: '是否确定将此商品置换成银元宝？',
				success: function(res) {
					if (res.confirm) {
						uni.showLoading({
							title: '数据提交中'
						});
						_this.$request.order
							.substitution({
								order_id: _this.order_id
							})
							.then(data => {
								uni.hideLoading();
								if (data.status == 1) {
									uni.showToast({
										icon: 'none',
										title: '置换成功'
									});
									setTimeout(() => {
										_this.initData();
									}, 1000)
								} else {
									uni.showToast({
										icon: 'none',
										title: data.msg
									});
								}
							})
							.catch(err => {
								uni.hideLoading();
								//消息异常
								uni.showToast({
									icon: 'none',
									title: '数据加载异常'
								});
							});
					}
				}
			})
		},
		openPwd() {
			//验证密码
			if (this.transferData.pay_type == 3) {
				this.transferPay();
			} else {
				this.$refs.paymentPassword.modalFun('show');
			}
		},
		transferPay(e) {
			//转拍下单
			var _this = this;
			uni.showLoading({
				title: '数据提交中'
			});
			var pwdPay = '';
			if (e) pwdPay = e.value;
			_this.$request.order
				.transfer({
					order_id: _this.order_id,
					type: _this.transferData.pay_type,
					pwdPay: pwdPay
				})
				.then(data => {
					uni.hideLoading();
					if (data.status == 1) {
						if (this.transferData.pay_type == 3) {
							_this.pays(data.msg);
						} else {
							uni.showToast({
								icon: 'none',
								title: '转拍成功'
							});
							setTimeout(() => {
								_this.$refs.paymentPassword.modalFun('hide');
								_this.$refs['showtransfer'].close();
								_this.initData();
							}, 1000)
						}
					} else {
						_this.$refs.paymentPassword.modalFun('hide');
						uni.showToast({
							icon: 'none',
							title: data.msg
						});
					}
				})
				.catch(err => {
					_this.$refs.paymentPassword.modalFun('hide');
					uni.hideLoading();
					//消息异常
					uni.showToast({
						icon: 'none',
						title: '数据加载异常'
					});
				});
		},
		pays(orderInfo) {
			jwx({
				nonceStr: orderInfo.nonceStr,
				timeStamp: orderInfo.timeStamp,
				package: orderInfo.package,
				signType: orderInfo.signType,
				paySign: orderInfo.paySign
			}, res => {
				uni.showToast({
					icon: 'none',
					title: '转拍成功'
				});
				setTimeout(() => {
					_this.initData();
				}, 1000)
			}, fail => {
				console.log(fail)
				uni.showToast({
					icon: 'none',
					title: '支付失败'
				});
			})
		},
		payChange(e) {
			this.transferData.pay_type = e.detail.value;
		},
		getLiveTimeCount(startTime) {
			//倒计时
			if (startTime) { //如果设置了开始时间
				this.liveCountTimes = setInterval(() => {
					//注：不论安卓还是ios，请将时间如 2020-02-02 20:20:20 转化为 2020/02/02 20:20:20 这种形式后再使用，否则无法转换，如下转换即可↓
					let transedPreTime = startTime.replace(/-/g, '/') //这里转化时间格式为以/分隔形式
					let nowTime = new Date().getTime();
					let preTime = new Date(transedPreTime).getTime()
					let obj = null;
					if (preTime - nowTime > 0) {
						let time = (preTime - nowTime) / 1000;
						let day = parseInt(time / (60 * 60 * 24));
						let hou = parseInt(time % (60 * 60 * 24) / 3600);
						let min = parseInt(time % (60 * 60 * 24) % 3600 / 60);
						let sec = parseInt(time % (60 * 60 * 24) % 3600 % 60);
						obj = {
							day: day < 10 ? '0' + day : day,
							hou: hou < 10 ? '0' + hou : hou,
							min: min < 10 ? '0' + min : min,
							sec: sec < 10 ? '0' + sec : sec
						};
						this.liveCountdown = "";
						if (obj.day != '00') {
							this.liveCountdown += obj.day + '天';
						}
						if (obj.hou != '00') {
							this.liveCountdown += obj.hou + ':';
						}

						this.liveCountdown += obj.min + ':' + obj.sec
					} else {
						obj = {
							day: '00',
							hou: '00',
							min: '00',
							sec: '00'
						};
						this.liveCountdown = "";
						if (obj.day != '00') {
							this.liveCountdown += obj.day + '天';
						}
						if (obj.hou != '00') {
							this.liveCountdown += obj.hou + ':';
						}

						this.liveCountdown += obj.min + ':' + obj.sec
						clearInterval(this.liveCountTimes)
					}
				}, 1000)
			} else {
				this.liveCountdown = "开始时间待定"
			}
		},
		getDate(item) { // 时间戳转日期
			var time = new Date(item)
			var year = time.getFullYear()
			var month = time.getMonth() + 1
			var date = time.getDate()
			var hours = time.getHours()
			var minutes = time.getMinutes()
			var seconds = time.getSeconds()
			return year + '-' + this.add0(month) + '-' + this.add0(date) + " " + this.add0(hours) + ":" + this.add0(
				minutes) + ":" + this.add0(seconds)
		},
		add0(m) {
			return m < 10 ? '0' + m : m
		}
	}
};
